<template>
  <div id="member-content">
    <div id="main-content-Product">
      <div class="left leftBox">
        <h1 id="bigTitle">{{!loading?message.enterpriseName:'数据正在加载... loading...'}}</h1>
        <div id="infoBox-member" v-if="message">
          <div class=textBox>
            <div class='textItem'><span class="type">企业类型: </span>
              <div class="content" v-text="message.enterpriseType||null"></div>
            </div>
            <div class='textItem'><span class="type">企业规模: </span>
              <div class="content" v-text="message.enterpriseScale||null"></div>
            </div>
            <div class='textItem'><span class="type">所在地: </span>
              <div class="content" v-text="message.location||null"></div>
            </div>
            <div class='textItem'><span class="type">注册资本: </span>
              <div class="content" v-text="message.registeredCapital||null"></div>
            </div>
            <div class='textItem'><span class="type">注册年份: </span>
              <div class="content" v-text="message.registeredDate||null"></div>
            </div>
            <div class='textItem'><span class="type">经营模式: </span>
              <div class="content" v-text="message.managementModel||null"></div>
            </div>
            <div class='textItem'><span class="type">经营范围: </span>
              <div class="content" v-text="message.managementScope||null"></div>
            </div>
            <div class='textItem'><span class="type">主营行业: </span>
              <div class="content" v-text="message.mainCamp||null"></div>
            </div>
            <div class='textItem'><span class="type">擅长应用: </span>
              <div class="content" v-text="message.mainApplication||null"></div>
            </div>
            <div class='textItem'><span class="type">发展历程: </span>
              <div class="content" v-text="message.developing||null"></div>
            </div>
            <div class='textItem'><span class="type">合作伙伴: </span>
              <div class="content" v-text="message.cooperativePartner||null"></div>
            </div>
            <div class='textItem'><span class="type">主要客户: </span>
              <div class="content" v-text="message.mainCustomer||null"></div>
            </div>
            <div class='textItem'><span class="type">邮政编码: </span>
              <div class="content" v-text="message.postalCode||null"></div>
            </div>
            <div class='textItem'><span class="type">联系电话: </span>
              <div class="content" v-text="message.contactNumber||null"></div>
            </div>
            <div class='textItem'><span class="type">传真: </span>
              <div class="content" v-text="message.fax||null"></div>
            </div>
            <div class='textItem'><span class="type">电子邮件: </span>
              <div class="content" v-text="message.email||null"></div>
            </div>
            <div class='textItem'><span class="type">联系地址: </span>
              <div class="content" v-text="message.contactAddress||null"></div>
            </div>
            <div class='textItem'><span class="type">联系人: </span>
              <div class="content" v-text="message.contacts||null"></div>
            </div>
            <div class='textItem'><span class="type">所在部门: </span>
              <div class="content" v-text="message.department||null"></div>
            </div>
            <div class='textItem'><span class="type">职务: </span>
              <div class="content" v-text="message.post||null"></div>
            </div>
            <div class='textItem'><span class="type">qq: </span>
              <div class="content" v-text="message.qq||null"></div>
            </div>
            <div class='textItem'><span class="type">wechat: </span>
              <div class="content" v-text="message.wechat||null"></div>
            </div>
            <div v-if="isApproval">
              <div class='textItem'><span class="type">资料认证: </span>
                <div class="content" v-text="message.authenticationData||null"></div>
                <el-button style="padding: 5px; margin-left: 10px;" type="primary" icon="el-icon-download" circle @click="downLoadFile(1)"></el-button>
              </div>
              <div class='textItem'><span class="type">联络人资料 </span>
                <div class="content" v-text="message.contactInfo||null"></div>
                <el-button style="padding: 5px; margin-left: 10px;" type="primary" icon="el-icon-download" circle @click="downLoadFile(2)"></el-button>
              </div>
            </div>
          </div>
        </div>
        <div v-if="loading">
          <noContent :loadName="'数据'"></noContent>
        </div>
      </div>
    </div>
  </div>

</template>

<script>
import { getMemberContent, downLoadMemberFile } from "@/api/getData";
import noContent from "@/components/index/content/no-content";

export default {
  name: "member-content",
  data() {
    return {
      hostUrl: "",
      message: "",
      loading: true,
      isApproval: false
    };
  },
  methods: {
    init() {
      //显示路径
      let index = this.$router.options.routes[0].children.findIndex(
        menu => menu.showName == "会员中心"
      );
      global.currentPath.data = [index, 2];
    },
    getData(url, id) {
      scroll(0, 300);
      let that = this;
      getMemberContent(url, id)
        .then(res => {
          that.loading = false;
          if (!res.message) return;
          that.message = res.message;
        })
        .catch(err => {
          that.loading = false;
          that.$message.error({
            showClose: true,
            message: "获取详细信息失败",
            duration: 2000
          });
        });
    },
    //下载文件
    downLoadFile(num) {
      let that = this;
      let file;
      if (num == 1) {
        file = that.message.authenticationDataUrl;
      } else if (num == 2) {
        file = that.message.contactInfoUrl;
      }
      let data = {
        tag: num,
        realFileName: file
      };
      const url = that.HOST + "/user/downloadMemberFile";
      downLoadMemberFile(url, data)
        .then(res => {
          if (res.result == "success") {
            let getUrl = this.HOST + res.message;
            window.location.href = getUrl;
          } else {
            that.$message.error({
              showClose: true,
              message: res.message,
              duration: 2000
            });
          }
        })
        .catch(err => {
          that.$message.error({
            showClose: true,
            message: "下载失败",
            duration: 2000
          });
        });
    }
  },
  activated() {
    this.init();
    let queryProp = JSON.parse(this.$route.query.queryMsg);
    let id = queryProp.id; //获取对象id
    if (queryProp.type == 2) {
      this.isApproval = true;
    }
    let url = this.HOST + "/user/getMemberInfo";
    this.loading = true;
    this.getData(url, id); //调用获取对象信息方法
  },
  components: { noContent }
};
</script>

<style lang="scss" scoped>
$blue: #127de7;
div#mainContent >>> * {
  max-width: 100%;
  white-space: pre-wrap;
}
div.left {
  width: 100%;
  padding: 0 15px;
  margin: 15px 0;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  display: inline-block;
  background: #fafafa;
  //   width: 100%;
  // /* padding: 0 15px; */
  // margin: 15px 0;
  // -webkit-box-sizing: border-box;
  // box-sizing: border-box;
  // /* display: inline-block; */
  // background: #fafafa;
}
div#mainContent {
  background: #fafafa;
  border: 1px solid #f1f1f1;
}
h1#bigTitle {
  text-align: center;
  padding: 30px;
}
div#infoBox-member {
  padding: 10px 0 30px;
  display: flex;
  align-items: center;
  justify-content: space-around;
}
div#innerContent {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}
div.apage {
  box-sizing: border-box;
  min-width: 51%;
  white-space: pre-line;
}
img#memberPic {
  display: block;
  max-width: 90%;
  margin: 0 auto;
}
div#information {
  color: gray;
  position: relative;
  left: 2rem;
  padding: 10px;
}
div.textBox {
  text-align: left;
  padding: 10px;
  min-width: 600px;
}
div.textItem {
  padding: 5px;
}
div.content {
  display: inline-block;
}
span.type {
  display: inline-block;
  margin-left: 2rem;
  margin-top: 4px;
  margin-bottom: 4px;
  margin-right: 0.5rem;
  font-weight: bold;
  width: 30%;
}
</style>



